.pricing-bg {
  background-color: #b3cdd1;
  background-image: linear-gradient(315deg, #b3cdd1 0%, #9fa4c4 74%);
}

.navBar {
  width: 100%;
  height: 3.5rem;
  background-color: rgba(255, 255, 255, 0.8);
  color: white;
  position: fixed;
  display: flex;
  padding-left: 10px;
  padding-right: 10px;
  justify-content: space-between;
}

.login-btn {
  height: 80%;
  margin: auto 0;
  width: 100%;
}

.nav-links {
  display: flex;
  list-style: none;
  justify-content: space-evenly;
  height: 100%;
  margin-top: 0;
}

.nav-links li {
  padding: 0;
  margin: auto;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  -webkit-transition: all 0.5s;
  transition: all 0.5s;
  height: 100%;
  background-color: #02b689;
  color: white;
  cursor: pointer;
}

.nav-links li a {
  color: white;
  font-weight: bold;
  text-decoration: none;
  -webkit-transition: all 0.5s;
  transition: all 0.5s;
  display: block;
  cursor: pointer;
  position: relative;
}

.nav-links li:hover {
  background-color: #f5183d;
}

.nav-links li:hover a {
  color: white;
}

.nav-links .active {
  background-color: #f5183d;
}

.nav-links .active a {
  color: white;
}

.drawer {
  width: 100%;
  margin: 20px;
}

.drawer-btn {
  display: none;
  font-size: 30px;
  color: white;
  background: transparent;
  border: none;
  cursor: pointer;
  outline: none;
  width: 50px;
  height: 50px;
}

.drawer-btn:focus {
  outline: none;
}

@media (max-width: 600px) {
  .navBar {
    padding-left: 0px;
    padding-right: 0px;
    justify-content: space-between;
  }
  .nav-links {
    display: block;
    list-style: none;
    -webkit-transition: all 0.5s;
    transition: all 0.5s;
    width: 100%;
    margin-top: 15px;
    margin: 15px auto;
  }
  .nav-links li {
    margin: 0 auto;
    text-align: center;
    height: 40px;
    width: 100%;
  }
  .drawer-btn {
    display: block;
    margin: auto;
  }
  .show-nav {
    display: flex;
    flex-direction: column;
    -webkit-transition: all 0.5s;
    transition: all 0.5s;
  }
  .hide-nav {
    -webkit-transition: all 0.5s;
    transition: all 0.5s;
    display: none;
  }
  .secondary-btn {
    background-color: #f5183d;
    border: none;
    color: black;
    padding: 3px 12px;
    font-size: 0.95rem;
    border-radius: 5px;
    font-weight: lighter;
    cursor: pointer;
    -webkit-transition: all  0.5s;
    transition: all  0.5s;
    display: flex;
    height: 1.8rem;
    font-size: 0.8rem;
    flex-direction: column;
    margin: auto;
    align-items: center;
    justify-content: center;
  }
  .secondary-btn:hover {
    background-color: rgba(0, 0, 0, 0.9);
    color: #f5183d;
  }
}

.single-price {
  text-align: center;
  background-color: #fff;
  margin-top: 20px;
  width: 25%;
  border-radius: 30px;
  overflow: hidden;
  -webkit-transition: all 0.5s;
  transition: all 0.5s;
  border-right: 1px solid gray;
  border-left: 1px solid gray;
}

.single-price h3 {
  font-size: 16px;
  text-align: center;
  margin: 0;
  margin-top: -100px;
  color: #fff;
}

.single-price h4 {
  margin-top: 10px;
  font-size: 20px;
  font-weight: bold;
  color: #fff;
}

.single-price h4 .sup {
  vertical-align: text-top;
  font-size: 25px;
}

@media (max-width: 600px) {
  .single-price {
    width: 100%;
  }
}

.deal-top {
  font-size: 16px;
  text-transform: uppercase;
  padding: 120px 24px 5px;
  -webkit-transition: all 0.5s;
  transition: all 0.5s;
  height: 80px;
}

.deal-bottom ul {
  margin: 0;
  padding: 0;
}

.deal-bottom ul li {
  font-size: 16px;
  font-weight: 300;
  border-bottom: 1px solid #ddd;
  align-items: center;
  -ms-grid-row-align: center;
  align-self: center;
  margin: 5px 0;
  padding: 5px 0px;
  height: 50px;
  list-style: none;
  font-weight: bold;
}

@media (max-width: 600px) {
  .deal-bottom ul li {
    height: auto;
  }
}

.btn-area a {
  display: inline-block;
  -webkit-transition: all 0.5s;
  transition: all 0.5s;
  font-size: 18px;
  color: white;
  padding: 8px 24px;
  margin-top: 32px;
  cursor: pointer;
  margin-bottom: 40px;
  font-weight: bold;
  text-decoration: none;
}

.tick-img {
