@import './assets//styles/variables';
@import './assets/styles/mixins';
@import url('https://fonts.googleapis.com/css?family=Comfortaa|Comic+Neue&display=swap');
@import url('https://fonts.googleapis.com/css?family=Fira+Sans&display=swap');

* {
  
  font-family: 'Comic Neue', cursive, sans-serif;
}

.vertical-align{
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;

}
.subheading{
  color: $secondaryTextColor;
  font-size: $subHeadingTextSize;
}
.heading{
  font-family: $headingFont;
  // font-size: 5rem;
}
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}


.err-txt{
  font-size: $errTextSize;
  color: $errorTextColor;
}
.small-txt{
  font-size: $errTextSize;
  color: $secondaryTextColor;
}
.main-div-width{
  width: $mainDivWidth;
  min-height: 100vh;
  margin:0 auto ;
}
.gradientbg{
  background-color: #fd8f67;
  background: linear-gradient(0deg,#fd8f67,#d463a3);
  background-image: linear-gradient(0deg, rgb(253, 143, 103), rgb(212, 99, 163));
  background-color: #fd8f67;

}

.nav-margin{
  margin-top:0;
  @media (max-width: 600px){
    margin-top:$navHeight; 
  }
}
.hover-color-change{
  color:gray;
  transition: all  0.5s !important;
  &:hover{
    color:black;
  }
}
.logo{
  width: 6rem;
  height: 6rem;
  margin: 0 auto;
}
.app-heading{
  margin:auto;
  color: rgb(255, 0, 119);
  font-weight: bolder;
  font-family: $headingFont;
}
.medium-txt{
  font-size: 1.2rem;
}
.small-txt{
  font-size: 0.8rem;
}
.large-txt{
  font-size:1.6rem;
}
.effect-btn{
  @include plainbtn();
  border: 1px solid wheat;
  padding: 5px 15px;
  color: wheat;
  font-weight: bold;
  &:hover{
    background-color: wheat;
    color: $secondaryTextColor;
  }
}
.color-btn{
  @include btn($btn1Bg,$btn1Text);
  
  
}
.btn{
  outline: none;
  border:none;
  @include plainbtn();
  // &:hover{
  //     color: $hoveredTextColor;
  // }
}  

